<template>
	<div class="workposi">
		<img src="../assets/images/workicon.jpg" class="posibtn" @click="posi_deta">
		<transition name="workAmi">
			<div class="posi_detail_div" v-show="posi_bol">
				<myheader style="background:#eee">
					<p class="posi_det_div_title">提问</p>
					<i class="closeIcon" @click="close_detail"><返回</i>
					<p class="posi_det_div_close">完成</p>
				</myheader>
				<div class="posi_detail_div_des">
					<h3 class="posi_detail_div_des_title">写下你的问题（必填）</h3>
					<textarea placeholder="增加问题补充，如问题背景等（选填）" rows="13" class="posi_detail_div_des_font"></textarea> 
				</div>
				<div class="posi_detail_div_sub">
					<h4 class="posi_detail_div_sub_title">所属话题<i>></i></h4>
					<form class="posi_detail_div_sub_bottom">
						<input type="radio"><label class="posi_detail_div_sub_des"> &nbsp; &nbsp;匿名你的提问需遵守《拉勾社区管理规范》</label> 
					</form>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	import Myheader from './myheader'
	export default{
		components:{
			Myheader
		},
		data (){
			return {
				posi_bol:false
			}
		},
		methods:{
			posi_deta (){
				this.posi_bol=true
			},
			close_detail (){
				this.posi_bol=false
			}
		}
	}
</script>

<style>
	.workAmi-enter{
		opacity: 0;
		transition: all .5s;
		transform: translateY(-200px);
	}
	.workAmi-enter-active{
		transition: all .5s;
	}
	.workAmi-leave{
		opacity: 1;
		transition: all .5s;
	}
	.workAmi-leave-active{
		opacity: 0;
		transition: all .5s;
		transform: translateY(200px);

	}
	.posi_detail_div_sub_bottom{
		height: 0.6rem;
		line-height: 0.6rem;
	}
	.posi_detail_div_sub_des{
		font-size: .24rem;
	}
	.posi_detail_div_sub{
		margin: 20px;

	}
	.posi_detail_div_sub i{
		float: right;
		color: #999;
	}
	.posi_detail_div_sub_title{
		font-size: 0.28rem;
		color: #666;
		border-bottom: 1px solid #eee;
		padding-bottom: 15px;
	}
	.posi_detail_div_des_font{
		width: 100%;
		border: none;
		outline: none;
		text-indent: 1em;
		margin-top: 10px;
	}
	.posi_detail_div_des{
		padding:0 20px;
		border-bottom: 10px solid #eee;
	}
	.posi_detail_div_des_title{
		font-size: 0.28rem;
		border-bottom: 1px solid #eee;
		padding:10px 0;
		color: #999;
	}
	.closeIcon{
		position:absolute;
		left: 0.34rem;
		top: 0.34rem;
		font-size: 0.34rem;
	}
	.posi_det_div_title{
		font-size: 0.34rem;
		line-height: 1.2rem;
		font-weight: bold;
		text-align: center;
	}
	.posi_det_div_close{
		position: absolute;
		right: 0.34rem;
		top:0.34rem;
		font-size: 0.34rem;
	}
	.posi_detail_div{
		width: 100%;
		height: 100%;
		z-index: 100;
		position: fixed;
		left: 0;
		top: 0;
		padding-top: 1.2rem;
		background-color: #fff;
		text-align: left;
	}
	.posibtn{
		width: 1rem;
		height: 1rem;
		background-color: #00b38a;
		border-radius: 50%;
		position: fixed;
		bottom: 1.5rem;
		right: 0.5rem;
	}
	.workposi{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;

	}
</style>


