<template>
	<div class="minetop">
		<span><slot name="touxiang"></slot></span>
		<span v-text="names" class="names"></span>
		<p v-text="myself" class="myself"></p>
	</div>
</template>

<script>
	export default{
		props:['names','myself']
	}
</script>

<style>
	.minetop{
		height: 200px;
		width: 100%;
		background-color: #00b38a;
		
	}
	.touxiang_img{
		width: 60px;
		height: 60px;
		border-radius: 50%;
		margin-top: 50px;
	}
	.names{
		font-size: 16px;
		color: #fff;
		display: block;
		padding: 10px 0;
	}
	.myself{
		color: rgba(255,255,255,0.6);
		font-size: 0.24rem;
	}
</style>


