<template>
	<div class="item" @click="changeImg()">
		<div v-show="bol" class="wrapicon"><slot name="normalImg"> </slot></div>
		<div v-show="!bol" class="wrapicon"><slot name="activeImg"> </slot></div>
		<p v-text="txt" :class="{ac:!bol,tab_name:true}"></p>
	</div>
</template>
<script>
	export default{
		props:['txt','id'],
		computed:{
			bol:function(){
				if(this.id==this.$parent.sel){
					return false
				}
				return true
			}
		},
		methods:{
			changeImg:function(){
				this.$emit('change',this.id);
				this.$router.push('/'+this.id)
			}
		}
	}
</script>
<style>
	.item{
		flex-grow: 1;

	}
	.wrapicon{
		height: 60%;
	}
	.icon{
		width: 0.6rem;
		height: 0.6rem;
		vertical-align: top;
	}
	.tab_name{
		font-size: 0.24rem;
		color: #666;
	}
	.ac{
		color:  #00b38a;
	}
</style>


