<template>
	<div class="swiper-container" :class="containeid">
		<div class="swiper-wrapper">
			<slot name="bannerImg"></slot>
		</div>
		<div :class="{'swiper-pagination':pagination}" :style="{'text-align':paginationDirection}"></div>
	</div>
</template>

<script>
import '../assets/libs/swiper/js/swiper.js'
	export default{
		props:{
			containeid:{//设置多个轮播图的唯一标示
				type:String,//值的类型
				default:""//默认值是什么
			},
			pagination:{//设置轮播图是否有分页器
				type:Boolean,
				default:true
			},
			paginationDirection:{//设置分页器的位置，有居左，居中，居右
				type:String,
				default:'center'
			},
			
			loop:{
				type:Boolean,
				default:true
			},
			autoplay:{
				type:Number,
				default:2000
			}

		},
		mounted:function(){
			var _this=this;
			new Swiper("."+_this.containeid,{
				loop:_this.loop,
				autoplay:_this.autoplay,
				pagination:'.swiper-pagination'

			})
		}
	}
</script>

<style>
	@import '../assets/libs/swiper/css/swiper.css';
	.swiper-container{width: 100%;}
	.swiper-container img{width: 100%;}

</style>


