<template>
	<div class="tabberWrap">
		<wrap :sel="select">
			<item txt="首页" id="home" @change='getVal'>
				<img src="../assets/images/ic_tab_home_normal.png" slot="normalImg" class="icon">
				<img src="../assets/images/ic_tab_home_active.png" slot="activeImg" class="icon">
			</item>
			<item txt="消息" id="massage" @change='getVal'>
				<img src="../assets/images/ic_chat_nomarlImg.png" slot="normalImg" class="icon">
				<img src="../assets/images/ic_chat_green.png" slot="activeImg" class="icon">
			</item>
			<item txt="言职" id="work" @change='getVal'>
				<img src="../assets/images/ic_tab_status_normal.png" slot="normalImg" class="icon">
				<img src="../assets/images/ic_tab_status_active.png" slot="activeImg" class="icon">
			</item>
			<item txt="我的" id="mine" @change='getVal'>
				<img src="../assets/images/ic_chat_action_emoji_normal.png" slot="normalImg" class="icon">
				<img src="../assets/images/ic_chat_action_emoji_pressed.png" slot="activeImg" class="icon">
			</item>
		</wrap>
	</div>
</template>

<script>
	import Wrap from './wrap'
	import Item from "./item"
	export default{
		components:{
			Wrap,
			Item
		},
		data (){
			return{
				select:"home"
			}
		},
		methods:{
			getVal:function(val){
				this.select=val;
			}
		}
	}
</script>
<style>
	.tabberWrap{position: fixed;bottom: 0;left: 0;width: 100%;background: #f7f7f7;}
</style>


