<template>
	<div class="home_main_list">
		<div  class="home_main_list_top" v-for="(item,index) in massagearr" @click="work_des(index)">
			<h3 class="job_text">{{ item.job }}</h3><i></i>
			<p class="working_text"><span>{{ item.address }}</span> | <span>{{ item.working }}</span> | <span>{{ item.schooling }}</span></p><p class="money_p">{{item.money}}</p>
			<div class="study_icon_box">
				<span v-for="(item2,index) in item.study"  class="study_icon">{{ item2 }}</span>
			</div>
			<div class="office">
				<img :src="item.src" class="office_img">
				<h5>{{item.officename}}</h5>
				<p>{{item.des}}</p>
			</div>
		</div>
		<transition-group name="detailAmi" tag="div">
			
			<div class="detail"v-show="current==index"  v-for="(item,index) in massagearr" :key="item.id">
				<div class="detailbox_top">
					<p class="home_det_top"><span class="colseIcon" @click="closefn()">< 返回</span>职位详情</p>
				</div>
				<div class="detailbox_main">
					<div class="detailbox_main_name">
						<h3 class="home_title">{{ item.job }}<span class="main_name_span">【{{ item.money }}】</span></h3>
						<span class="name_list">
							<i class="address_icon"></i>
							<span>{{ item.address }}</span>
						</span>
						<span class="name_list">
							<i class="working_icon"></i>
							<span>{{ item.working }}</span>
						</span>
						<span class="name_list">
							<i class="school_icon"></i>
							<span>{{ item.schooling }}</span>
						</span>
						<span class="name_list">
							<i class="job_icon"></i>
							<span>实习</span>
						</span>
						<p class="detailbox_main_name_text">职位诱惑:双休五险，带薪年假，全勤餐补，节日礼金</p>
						<div class="office">
							<img :src="item.src"  class="office_img">
							<span class="officename"><strong>{{ item.officename }}</strong></span>
							<span class="office_txt">{{ item.des }}</span><i class="office_icon">></i>
						</div>
					</div>
					<div class="work_des">
						<h3>职位描述</h3>
						<dl>
							<dt>岗位职责</dt>
							<dd>1、根据工作安排高效、高质地完成代码编写，确保符合规范的前端代码规范;</dd>
							<dd>2、负责公司现有的项目和新项目的前端修改调试和开发工作;</dd>
							<dd>3、与设计团队紧密配合，确保代码有效对接，优化网站前端性能</dd>
							<dt>补充说明</dt>
							<dd>1、计算机科学与技术大专以上学历;</dd>
							<dd>2、两年以上全职Web前端开发经验;</dd>
							<dd>3、熟练使用HTML、CSS、JavaScript,了解各浏览器的差异、原理;</dd>
						</dl>
					</div>
				</div>
			</div>
		</transition-group>
	</div>
</template>
<script>
	export default{
		data (){
			return{
				current:-1,
				massagearr:[
				{
					job:"web前端实习生",address:"重庆 | 九龙坡区",working:"应届毕业生",schooling:"本科",study:["Javascript","Css","Angularjs"],money:"2-4K",
					src:require("../assets/images/office_icon.jpg"),officename:"聚通科技",des:"成长型（不需要融资）| 50-150人 | 移动互联网","id": 0
				},
				{
					job:"前端工程师",address:"青岛  | 松岭路",working:"1年以下",schooling:"大专",study:["web前端","js"],money:"3-5K",src:require("../assets/images/office_icon1.jpg"),officename:"乐商科技",des:"成长型（不需要融资）| 50-150人 | 移动互联网","id": 1
				},
				{
					job:"前端开发",address:"东莞 | 东莞市市区",working:"1-3年",schooling:"大专",study:["资深","高级","中级","web","HTML"],money:"4-6K",src:require("../assets/images/office_icon2.jpg"),officename:"100课堂",des:"成长型（不需要融资）| 50-150人 | 移动互联网","id": 2
				},
				{
					job:"前端工程师",address:"佛山 | 乐从",working:"1-3年",schooling:"大专",study:["Javascript","web","初级"],money:"4-7K",src:require("../assets/images/office_icon3.jpg"),officename:"聊百易购",des:"成长型（不需要融资）| 50-150人 | 移动互联网","id": 3
				}
				]
			}
			
		},
		methods:{
	    
			work_des:function(index){
				// 职业详情显示页
				this.current=index; 
			},
			closefn:function(){
				// 关闭按钮
				this.current=-1; 
			}   
	    }
	}
</script>
<style>
	.home_title{
		font-size: 0.28rem;
	}
	.home_det_top{
		font-size: .24rem;
	}
	.detailAmi-enter{
		opacity: 0;
		transition: all .5s;
		transform: translateX(300px);
	}
	.detailAmi-enter-active{
		transition: all .5s;
	}
	.detailAmi-leave{
		opacity: 1;
		transition: all .5s;
	}
	.detailAmi-leave-active{
		opacity: 0;
		transition: all .5s;
		transform: translateX(-300px);

	}
	dt{
		padding: 0.17rem 0;
		font-size: 0.28rem;
	}
	dd{
		line-height: 0.43rem;
		font-size: 0.24rem;
		color: #666;
	}
	.office .office_icon{
		float: right;
		font-size: 0.24rem;
	}
	.office_txt{
		font-size: 0.21rem;
		color: gray;
		padding-top: 5px;
		float: left;
	}
	.work_des{
		width: 100%;
		border-top: 10px solid #eee;
		clear: both;
		padding-top: 10px;
	}
	.work_des h3{
		font-size: 0.28rem;
		line-height: 0.6rem;
	}
	.detailbox_main_name_text{
		font-size: 0.2rem;
		color: gray;
		clear: both;
		padding: 15px 0;
		border-bottom: 1px solid lightgray;
	}
	.detailbox_main{
		margin:10px 20px;
	}
	.detailbox_main_name .main_name_span{
		float: none;
		display: inline;
		color: red;
	}
	.detailbox_main_name{
		margin-top: 10px;
		padding-bottom: 60px;
	}
	
	.detailbox_main_name span{
		display: block;
		float: left;
	}
	.detailbox_main_name i{
		float: left;
		width: 0.22rem;
		height: 0.28rem;
		margin-right: 5px;
	}
	.name_list{
		margin-top: 10px;
		font-size: 0.24rem;
	}
	.officename{
		font-size: 0.24rem;
	}
	.address_icon{
		
		background-image: url("../assets/images/detail_icon_05.jpg");
		background-repeat: no-repeat;
		background-position: 0 3px;
	}
	.working_icon{
		background-image: url("../assets/images/detail_icon_12.jpg");
		background-repeat: no-repeat;
		background-position: 0 3px;
	}
	.school_icon{
		background-image: url("../assets/images/detail_icon_14.jpg");
		background-repeat: no-repeat;
		background-position: 0 4px;
	}
	.job_icon{
		background-image: url("../assets/images/detail_icon_07.jpg");
		background-repeat: no-repeat;
		background-position: 0 3px;
	}
	.detail{
		position: fixed;
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
		z-index: 100;
		overflow: auto;
		background-color: #fff;
	}
	
	.detailbox_top{
		width: 100%;
		height: 70px;
		background-color: #f7f7f7;
		line-height: 1.2rem;
		text-align: center;
	}
	.colseIcon{
		position: absolute;
		left: 20px;
		top: 4px;
		font-size: 0.34rem;
	}
	.home_main_list{
		
		text-align: left;

	}
	.home_main_list_top{
		position: relative;
		padding:15px;
		border-top: 10px solid #eee;
		border-bottom: 5px solid #eee;
	}
	.study_icon{
		display: flex;
		float: left;
		padding:5px 10px;
		background-color: #eee;
		margin:10px 5px;
		font-size: 0.24rem;
	}
	.money_p{
		position: absolute;
		right: 0.86rem;
		top:0.34rem;
		font-size:0.28rem; 
	}
	.working_text{
		font-size: 0.2rem;
	}
	.working_text span{
		color: #666;
	}
	.job_text{
		font-size: 0.24rem;
		padding-bottom: 5px;
	}
	.study_icon_box{
		height: 0.86rem;
	}
	.office_img{
		width: 0.6rem;
		float: left;
		padding-right: 10px;
	}
	.office{
		margin-top: 20px;
	}
	.office h5{
		font-size: 0.24rem;
		font-weight: normal;
	}
	.office p{
		font-size: 0.2rem;
		color: #999
	}
</style>


