<template>
  <div id="app">
      <router-view></router-view>
      <Tabber></Tabber>
  </div>
</template>

<script>
import Tabber from './components/tabber'
export default {
  name: 'app',
  created:function () {
    
    this.$router.push('/')
  },
  components:{
    Tabber
  }
}
</script>

<style>
*{padding :0;margin:0;}
ul{list-style: none;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



