<template>
	<div class="worklist">
		<div class="posi"></div>
		
			<div class="work_list_item" v-for="(item,index) in workarr">
				<p class="from_sty">一个新问题来自&nbsp;&nbsp;<span class="workfrom">{{ item.workfrom }}</span></p>
				<p class="workdes">{{ item.workdes }}</p>
				<div class="work_list_item_des">
					<img :src="item.worksrc" class="worksrc">
					<span class="worker">{{ item.worker }}<em class="workname">{{item.workname}}</em><span class="workdate">{{ item.workdate }}</span></span>
					<p class="workdetail">{{item.workdetail}}</p>
					<div class="sub_spa_box">
						<img src="../assets/images/ic_fill_voted_large.png" class="subicon">
						<span class="subtxt">{{item.sub}}</span>
					</div>
					<div class="sub_spa_box">
						<img src="../assets/images/ic_forum_topic_comment.png" class="speakicon">
						<span class="speaktxt">{{item.speaking}}</span>
					</div>
				</div>
		</div>
		
	</div>
</template>

<script>
	export default{
		data (){
			return{
				workarr:[
					{workfrom:"职业发展",workdes:"流程完善的大公司与创业公司如何抉择?",worksrc:require("../assets/images/bottom_icon_03.jpg"),worker:"jian",workname:"软件工程师",workdate:"06月07日",workdetail:"曾在腾讯，Facebook之类的大公司实习过，也在一些B轮小公司实习过。",sub:200,speaking:"19"},
					{workfrom:"运营",workdes:"新媒体运营编辑的出路在哪里?",worksrc:require("../assets/images/bottom_icon_07.jpg"),worker:"郑嘉平",workname:"爱折腾，闲不住",workdate:"06月07日",workdetail:"曾在腾讯，Facebook之类的大公司实习过，也在一些B轮小公司实习过。",sub:285,speaking:"21"},
					{workfrom:"职业发展",workdes:"流程完善的大公司与创业公司如何抉择?",worksrc:require("../assets/images/icon.jpg"),worker:"jian",workname:"软件工程师",workdate:"06月07日",workdetail:"曾在腾讯，Facebook之类的大公司实习过，也在一些B轮小公司实习过。",sub:200,speaking:"19"},
					{workfrom:"职业发展",workdes:"流程完善的大公司与创业公司如何抉择?",worksrc:require("../assets/images/ic_tab_profile_normal.png"),worker:"jian",workname:"软件工程师",workdate:"06月07日",workdetail:"曾在腾讯，Facebook之类的大公司实习过，也在一些B轮小公司实习过。",sub:200,speaking:"19"},
				]
			}
		}
	}
</script>

<style>
	.sub_spa_box{
		float: left;
		margin-right: 15px;
		display: flex;
		padding-top: 15px;
	}
	.subtxt{
		font-size: 0.2rem;
	}
	.speaktxt{
		font-size: 0.2rem;
	}
	.workdetail{
		font-size: 0.24rem;
		color: #666
	}
	.posi{
		width: 100%;
		height: 1.2rem;
	}
	.subicon{
		width: 0.4rem;
		height: 0.4rem;
	}
	.speakicon{
		width: 0.26rem;
		height: 0.26rem;
		padding-top: 5px;
		padding-right: 5px;
	}
	.worklist{
		text-align: left;
		padding:0 20px;
		margin-bottom: 1.2rem;
		overflow: hidden;
	}
	.work_list_item{
		width: 100%;
		border-bottom: 10px solid #eee;
		padding: 10px 0;
	}
	.work_list_item::after{
		content: "";
		display: block;
		clear: both;
	}
	.from_sty{
		font-size: 0.2rem;
		color: gray;
	}
	.workfrom{
		font-size: 0.24rem;
		color: #000;
	}
	.workdes{
		font-size: 0.24rem;
		padding-top: 5px;
	}
	.work_list_item_des{
		padding-top: 20px;
	}
	.worksrc{
		width: 0.52rem;
		float: left;
		padding-right: 8px;
	}
	.worker{
		padding:8px 0;
		font-size: 0.24rem;
		display: block;
	}
	.workname{
		font-style: normal;
		font-size: 0.2rem;
		padding-left: 20px;
		color: gray;
	}
	.workdate{
		float: right;
		color: lightgray;
	}
</style>


