<template>
	<div class="myheader" :style="{background:col}">
		<slot></slot>
	</div>
</template>


<script>
	export default{
		
		 data() {
		      return {
		        	scroll: '',
		        	col:"transparent",
		      }
	    },
	    methods: {
	     	 menu() {
	     	 	var oMh=document.getElementsByClassName('myheader')[0]
		        this.scroll = document.body.scrollTop;
			    if(this.scroll>=130){
			      	this.col="rgba(0,179,138,1)"
			      }else if(this.scroll<130){
			      	this.col="rgba(0,179,138,0)"
			      }
		     }
	    },
	    mounted() {
	      window.addEventListener('scroll', this.menu)
	      
		      
	    }
	}
</script>
<style>
	.myheader{
		height:1.1rem;
		width: 100%;
		position: fixed;
		left: 0;
		top:0;
		z-index: 100;
	}
	.searchBox{
		width: 70%;
		height: 0.5rem;
		background-color: #fff;
		border-radius: 20px;
		border: none;
		outline: none;
		float: left;
		margin:15px 25px;
		text-align: center;
		color: #999;
	}
	.phoneImg{
		width:0.5rem;
		padding: 15px 0px;
		float: left;
		color: #fff;
	}
	.searchIcon{
		position: absolute;
		left: 25%;
		top:0.34rem;
		width: 0.34rem;
	}
</style>


